    margin: 0 auto;
    width: 940px;
    display: grid;
    grid-template-columns: 147px 620px;
    column-gap: 172px;
  }
}
@media (min-width: 1200px) {
  .page-header__wrapper {
    column-gap: 168px;
  }
}
