.page-header__logo svg {
  fill: #ffffff;
}
.page-header__logo:hover svg {
  fill: #d22856;
  opacity: 1;
}
.page-header__logo:active svg {
  fill: #ffffff;
  opacity: 0.3;
}
.page-header__logo:focus {
  fill: #ffffff;
  opacity: 0.3;
  outline: none;
}
.page-header__toggle {
  position: absolute;
  top: 21px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 24px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media (min-width: 1200px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__menu-burger--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
@media (min-width: 660px) {
  .page-header__menu-burger--closed .page-header__toggle::before {
    top: 23px;
  }
}
.page-header__menu-burger--closed .page-header__toggle:hover::before {
  background-color: #D22856;
  box-shadow: 0 9px 0 0 #D22856, 0 17px 0 0 #D22856;
}
.page-header__menu-burger--closed .page-header__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 9px 0 0 rgba(210, 40, 86, 0.3), 0 17px 0 0 rgba(210, 40, 86, 0.3);
}
.page-header__menu-burger--opened .page-header__toggle::before,
.page-header__menu-burger--opened .page-header__toggle::after {
  content: "";
  position: absolute;
  display: block;
  top: 11px;
  left: 20px;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
}
.page-header__menu-burger--opened .page-header__toggle::before {
  transform: rotate(45deg);
}
.page-header__menu-burger--opened .page-header__toggle::after {
  transform: rotate(-45deg);
}
.page-header__menu-burger--opened .page-header__toggle:hover::before,
.page-header__menu-burger--opened .page-header__toggle:hover::after {
  background-color: #D22856;
}
.page-header__menu-burger--opened .page-header__toggle:active::before,
.page-header__menu-burger--opened .page-header__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.page-header__menu-burger--nojs .page-header__toggle {
  display: none;
}
